<script setup>
import { reactive, ref, computed } from "vue";

const cities = reactive([]);
const citySearch = ref("");

fetch("http://192.168.1.3:8081/cities.json")
  .then((res) => res.json())
  .then((data) => {
    data.forEach((el) => {
      cities.push(el);
    });
  });

const filteredCities = computed(() => {
  return cities.filter((city) => {
    return (
      city.city_name.toLowerCase().indexOf(citySearch.value.toLowerCase()) != -1
    );
  });
});

</script>

<template>
  <main>
    <div class="container-fluid">
      <div class="d-flex justify-content-center mt-4 p-4">
        <input type="text" placeholder="select city" class="form-control form-control-lg" v-model="citySearch"/>
      </div>
      <div class="row header">
        <div class="container">
          <h1 class="mb-4">Create a Custom List</h1>
          <p>
            Clicking on a category below will expand the choice to give you a list of all available contest within that option. 
            Check off all the contest you wish to view and hit the submit button at the bottom of the page. This will provide 
            you a custom view of only races you're interested in. If you aren't sure who the candidates are in the contest, clicking 
            on the info button to the right will provide a list of all the candidates.
          </p>
        </div>
      </div>
      <div class="row">
        <div class="container">
          <ul class="card-columns">
            <li class="city-select" v-for="(city, idx) in filteredCities" :key="idx">
              <a>{{ city.city_name }}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </main>
</template>