<script setup>
import { computed, reactive, ref } from "vue";

const stores = reactive([]);
const mySearch = ref("");

fetch("http://192.168.1.3:8080/sample.json")
  .then((res) => res.json())
  .then((data) => {
    data.forEach((el) => {
      stores.push(el);
    });
  });

const filtered = computed(
  () => stores.filter( //return each store with
    store => store.data.find( //a data item that contains at least one
      d => d.items.find( //items array that contains at least one
        item => item.includes(mySearch) //item that includes TERM
      )
    )
  )
)

console.log(filtered)

</script>


<template>
  <main>
    <div class="container-fluid">
      <div class="row header">
        <div class="d-flex justify-content-center mt-0 p-4">
          <input
            type="text"
            placeholder="select store"
            class="form-control form-control-lg"
            v-model="mySearch"
          />
        </div>
      </div>
      <div class="row">
        <div class="container">
          <div v-for="(store, index) in filtered" :key="index">
            <li>{{ store.store }}</li>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>